<template>
  <div class="projects">
    <b-container align-v="center">
      <b-card-group deck>
        <card
          v-for="job in displayJobs"
          :key="job.id"
          :name="job.name"
          :description="job.text"
        ></card>
      </b-card-group>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        first-text="First"
        prev-text="Prev"
        next-text="Next"
        last-text="Last"
        align="center"
        @input="paginate(currentPage)"
      ></b-pagination>
    </b-container>
  </div>
</template>

<script>
// @ is an alias to /src
import Card from "@/components/Card.vue";
import { mapGetters } from "vuex";

export default {
  name: "Projects",
  components: {
    card: Card
  },
  computed: {
    ...mapGetters(["jobs", "displayJobs", "rows"])
  },
  mounted() {
    this.fetchData();
  },
  data() {
    return {
      currentPage: 1,
      perPage: 3
    };
  },
  methods: {
    async fetchData() {
      await this.$store.dispatch("fetchJobs");
    },
    paginate(currentPage) {
      this.$store.dispatch("paginate", { currentPage, perPage: this.perPage });
    }
  }
};
</script>
